body{overflow:hidden}.opening{position:fixed;width:100%;height:100vh;z-index:1502;display:flex;justify-content:center;align-items:center}.opening .container-left{background-color:#2f394d;width:50%;position:absolute;z-index:1500;top:0;bottom:0;left:0;transition:width 1s}.opening .container-right{background-color:#2f394d;width:50%;position:absolute;z-index:1500;top:0;bottom:0;right:0;transition:width 1s}.opening .circle{width:350px;height:350px;border-radius:50%;background:#468c98;border:10px solid #40ed7a;position:absolute;z-index:1501;transition:all .5s;display:flex;justify-content:center;align-items:center}.opening .circle img{width:90%;-webkit-animation-name:logo-rotate;animation-name:logo-rotate;-webkit-animation-duration:1s;animation-duration:1s;-webkit-animation-iteration-count:1;animation-iteration-count:1;-webkit-animation-timing-function:ease-in-out;animation-timing-function:ease-in-out}.opening .slide-left{width:0}.opening .slide-right{width:0}@-webkit-keyframes logo-rotate{from{-webkit-transform:rotate(-360deg);transform:rotate(-360deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}@keyframes logo-rotate{from{-webkit-transform:rotate(-360deg);transform:rotate(-360deg)}to{-webkit-transform:rotate(360deg);transform:rotate(360deg)}}
