.matrix{box-sizing:border-box;height:100vh;background-color:#000;background-image:radial-gradient(#11581E, #041607),url("https://media.giphy.com/media/oEI9uBYSzLpBK/giphy.gif");background-repeat:no-repeat;background-size:cover;font-family:"Inconsolata",Helvetica,sans-serif;font-size:1.5rem;color:rgba(128,255,128,.8);text-shadow:0 0 1ex #3f3,0 0 2px rgba(255,255,255,.8)}.matrix .noise{pointer-events:none;position:absolute;width:100%;height:100%;background-image:url("https://media.giphy.com/media/oEI9uBYSzLpBK/giphy.gif");background-repeat:no-repeat;background-size:cover;z-index:-1;opacity:.02}.matrix .overlay{pointer-events:none;position:absolute;width:100%;height:100%;background:repeating-linear-gradient(180deg, rgba(0, 0, 0, 0) 0, rgba(0, 0, 0, 0.3) 50%, rgba(0, 0, 0, 0) 100%);background-size:auto 4px;z-index:1}.matrix .overlay::before{content:"";pointer-events:none;position:absolute;display:block;top:0;left:0;right:0;bottom:0;width:100%;height:100%;background-image:linear-gradient(0deg, transparent 0%, rgba(32, 128, 32, 0.2) 2%, rgba(32, 128, 32, 0.8) 3%, rgba(32, 128, 32, 0.2) 3%, transparent 100%);background-repeat:no-repeat;-webkit-animation:scan 7.5s linear 0s infinite;animation:scan 7.5s linear 0s infinite}@-webkit-keyframes scan{0%{background-position:0 -100vh}35%,100%{background-position:0 100vh}}@keyframes scan{0%{background-position:0 -100vh}35%,100%{background-position:0 100vh}}.matrix .terminal{box-sizing:inherit;position:absolute;height:100%;width:1000px;max-width:100%;padding:4rem;text-transform:uppercase}.matrix .output{color:rgba(128,255,128,.8);text-shadow:0 0 1px rgba(51,255,51,.4),0 0 2px rgba(255,255,255,.8)}.matrix .output::before{content:"> "}.matrix a{color:#fff;text-decoration:none}.matrix a::before{content:"["}.matrix a::after{content:"]"}.matrix .errorcode{color:#fff}.matrix .signature{font-size:1rem !important}
